//    backenda,    " ",    redux store   localStorage.
import { useDispatch } from 'react-redux';
import sprite from '../../images/sprite.svg';
import { IconLogoutStyled, LogoutStyled } from './LogOutBtn.styled';
import { logOutUser } from 'store/auth/operations';

//   dispatch  logoutUser, const handleLogOut    onClick  LogoutStyled.

function LogOutBtn() {
  const dispatch = useDispatch();
  const handleLogOut = () => {
    dispatch(logOutUser());
    
  };

  return (
    <>
      <LogoutStyled onClick={handleLogOut} >
        <span>Logout</span>
        <IconLogoutStyled>
          <use href={`${sprite}#icon-logout`} />
        </IconLogoutStyled>
      </LogoutStyled>
    </>
  );
}

export default LogOutBtn;
